export default "body.vscode-light {\n  background: #FFF;\n}\nbody.vscode-dark {\n  background: #334;\n}\n\n\ndiv {\n  color: #404040;\n  text-transform: uppercase;\n  text-align: center;\n  font-family: 'Arial', sans-serif;\n  font-size: 30px;\n  letter-spacing: 4px;\n}\n\nbody.vscode-dark div {\n  color: #DDD;\n}\n\n.preloader {\n  margin: 120px auto 0;\n}\n\n.preloader .line {\n  width: 1px;\n  height: 32px;\n  background: #000;\n  margin: 0 1px;\n  display: inline-block;\n  animation: opacity 1000ms infinite ease-in-out;\n}\n\nbody.vscode-dark .preloader .line {\n  background: #FFF;\n}\n\n.preloader .line-1 { animation-delay: 800ms; }\n.preloader .line-2 { animation-delay: 600ms; }\n.preloader .line-3 { animation-delay: 400ms; }\n.preloader .line-4 { animation-delay: 200ms; }\n.preloader .line-6 { animation-delay: 200ms; }\n.preloader .line-7 { animation-delay: 400ms; }\n.preloader .line-8 { animation-delay: 600ms; }\n.preloader .line-9 { animation-delay: 800ms; }\n\n@keyframes opacity { \n  0% { \n    opacity: 1;\n    height: 15px;\n  }\n  50% { \n    opacity: 0;\n    height: 12px;\n  }\n  100% { \n    opacity: 1;\n    height: 15px;\n  }  \n}";